import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    items: JSON.parse(localStorage.getItem('cartItems')) || [],
};

const cartSlice = createSlice({
    name: 'cart',
    initialState,
    reducers: {
        addItemToCart: (state, action) => {
            const { product, size, quantity } = action.payload;

            // Kim tra xem sn phm  tn ti trong gi hng cha
            const existingItemIndex = state.items.findIndex(item => item.id === product.id && item.size === size);

            if (existingItemIndex > -1) {
                // Nu sn phm  tn ti, cp nht s lng
                state.items[existingItemIndex].quantity += quantity;
            } else {
                // Nu sn phm cha tn ti, thm sn phm mi vo gi hng
                state.items.push({ ...product, size, quantity });
            }

            // Lu vo localStorage
            localStorage.setItem('cartItems', JSON.stringify(state.items));
        },
        removeItemFromCart: (state, action) => {
            const { id, size } = action.payload;
            state.items = state.items.filter(item => !(item.id === id && item.size === size));
            localStorage.setItem('cartItems', JSON.stringify(state.items));
        },
        clearCart: (state) => {
            state.items = [];
            localStorage.removeItem('cartItems'); // Xa gi hng khi localStorage
        },
    },
});

export const { addItemToCart, removeItemFromCart, clearCart } = cartSlice.actions;
export default cartSlice.reducer;
